body{
  font-family: Tahoma;
  margin: 0;
  padding: 0;
  text-align: center;
}

a{
  text-decoration: none;
  color: darkblue;
}
a:hover{
  text-decoration: underline;
}

img{
  width: 175px;
  height: 250px;
  border: 2px solid #aaa;
}

#header {
  background-color: #8C1B08;
  color: #fff;
  padding: 5px;
}

#header a{
  color: #fff;
  text-decoration: none;
}

.content{
  margin-top: 3rem;
}

#main table{
  margin: 0 auto;
  margin-top: 2rem;
}


#footer {
  font: 12px Tahoma;
  margin: 25px 0 50px 0;
}

#footer a{
  margin-right: 10px;
}